import React, { Component } from "react";
import "./style.css";
import lockImg from "./lock.png";

export default class UserAvatar extends Component {
  state = {
    choosed: false
  };

  render() {
    const user = this.props.user;
    let baseClasses = ["userContainer"];
    if (this.state.choosed) baseClasses.push("choosed");
    const borderColor = user.sex === 2 ? "blue" : "red";
    const lockIcon = <img src={lockImg} className="lock" alt="lock"></img>;

    return (
      <div className={baseClasses.join(" ")}>
        <img
          onClick={this.imgClick}
          style={{ borderColor: borderColor }}
          className="avatar"
          title={user.id}
          src={user.photo}
          alt=""
        ></img>
        {user.private && lockIcon}
        <a
          className="userName"
          href={user.url}
          target="_blank"
          rel="noopener noreferrer"
        >
          <div>{user.first_name}</div>
          <div>{user.last_name}</div>
        </a>
      </div>
    );
  }

  imgClick = () => {
    this.setState({
      choosed: !this.state.choosed
    });
  };
}
