import React, { Component } from "react";
import { Table } from "semantic-ui-react";
import "./style.css";

import InterestingProperty from "./InterestingProperty";

import {
  interestingProperties,
  importantProperties
} from "../../../defaultData.js";

export default class PropertiesPanel extends Component {
  render() {
    return (
      <div>
        <Table>
          <Table.Header>
            {importantProperties.map(item => (
              <Table.HeaderCell className="propertyTableHeader">
                {item.name}
              </Table.HeaderCell>
            ))}
          </Table.Header>
          <Table.Body>
            <Table.Row>
              {importantProperties.map(item => (
                <Table.Cell>{item.value}</Table.Cell>
              ))}
            </Table.Row>
          </Table.Body>
        </Table>
        <Table selectable fixed>
          <Table.Header>
            <Table.HeaderCell className="propertyTableHeader" width={10}>
              Название
            </Table.HeaderCell>
            <Table.HeaderCell className="propertyTableHeader" width={5}>
              Значение
            </Table.HeaderCell>
            <Table.HeaderCell width={2}></Table.HeaderCell>
          </Table.Header>
          <Table.Body>
            {interestingProperties.map(propertyItem => (
              <InterestingProperty propertyItem={propertyItem} />
            ))}
          </Table.Body>
        </Table>
      </div>
    );
  }
}
