import React, { Component } from "react";
import { Segment } from "semantic-ui-react";

import UserAvatar from "./UserAvatar/User";

import { usersData } from "../defaultData.js";

export default class EntitiesPanel extends Component {
  render() {
    return (
      <Segment compact>
        <br />
        <div>
          {usersData.slice(0, 20).map((user, idx) => (
            <UserAvatar user={user} key={user.id} index={idx} />
          ))}
        </div>
      </Segment>
    );
  }
}
